.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(5px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.split {
  height: 100%;
  width: 50%;
  position: absolute;
  overflow-x: hidden;
  padding-top: 10px;
  margin-top:7rem;
 
}

.left {
  left: 0rem;
  background-color: darkkhaki;
}

.right {
  right:0rem;
  background-color: rgb(160, 111, 47);
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  border-style: double;
}

h4{
  padding:0px;
  margin:0px;
}

.topnav {
  background-color: #333;
  overflow: hidden;
  margin-top:1rem;
}

/* Style the links inside the navigation bar */
.topnav a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;

}

/* Change the color of links on hover */
.topnav a:hover {
  background-color: #ddd;
  color: black;
}

/* Add an active class to highlight the current page */
.topnav a.active {
  background-color: #04AA6D;
  color: white;
}

/* Hide the link that should open and close the topnav on small screens */
.topnav .icon {
  display: none;
}


.container
{

  display:flex;
  flex-wrap: wrap;

  justify-content: space-between;
}

.card
{
  background: linear-gradient(#e66465, #9198e5);
  flex: 0 1 auto; /*flex-grow, flex-shrink, and the flex-basis*/
  border: 2px solid grey;
  text-align: center;
  letter-spacing: 0.2px;
  word-spacing: 0.1px;
  margin: auto;
  margin-bottom: 2vw;
  padding: auto;
  height:fit-content;
  
}

.card:hover{
    box-shadow: 6px 8px 8px 8px silver;

}

.formdesign
{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.inputdesign
{
  margin-top:1rem;
  margin-bottom:0.3rem;
width: 50%;
height:2rem;
}
.btndesign
{
  margin-top:3%;
  background-color: #9198e5;
  width:50%;
  height:2rem;
  cursor: pointer;
}